import React from "react"
import { Card, Form, Input, Button, message, Tabs } from "antd"
import { JsonEditor as Editor } from "jsoneditor-react"
import "jsoneditor-react/es/editor.min.css"
import Terminal from "react-console-emulator"

import {
  getMakeCredentialsChallenge,
  sendWebAuthnResponse,
  getGetAssertionChallenge,
} from "./webAuthAPI"
import {
  preformatMakeCredReq,
  publicKeyCredentialToJSON,
  preformatGetAssertReq,
} from "./utils"

const { TabPane } = Tabs

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
}
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
}

class App extends React.Component {
  state = { options: {}, user: {}, response: null, challenge: "" }
  async componentDidMount() {
    const a = await window.PublicKeyCredential.isUserVerifyingPlatformAuthenticatorAvailable()
    if (!a) {
      message.error("Webauthnn not support")
    }
    console.log(a)
  }

  createWebauthConfig(username, displayName) {
    const challenge = Buffer.alloc(32)
    const userId = Buffer.alloc(32, 1)
    const options = {
      publicKey: {
        challenge: challenge,

        rp: {
          name: "webauthnplay.herokuapp.com",
        },

        user: {
          id: userId,
          name: username,
          displayName: displayName,
        },

        attestation: "direct",

        pubKeyCredParams: [
          {
            type: "public-key",
            alg: -7, // "ES256" IANA COSE Algorithms registry
          },
        ],
        authenticatorSelection: { authenticatorAttachment: "platform" },
      },
    }

    this.setState({
      challenge: challenge,
      user: { id: Buffer.alloc(32), name: username, displayName: displayName },
      options: options,
    })
    return options
  }

  createWebauthGetConfig() {
    const { challenge, user, response } = this.state
    return {
      publicKey: {
        challenge: challenge,
        allowCredientails: [
          {
            type: "public-key",
            id: "test",
            transports: ["internal"],
          },
        ],
      },
    }
  }

  handleRegister = async (values) => {
    // event.preventDefault()
    const { username, name = "auto" } = values

    if (!username || !name) {
      alert("Name or username is missing!")
      return
    }
    this.setState({})
    const that = this

    const options = this.createWebauthConfig(username, name)

    try {
      const response = await navigator.credentials.create(options)

      console.log(response)
      this.setState({ response: publicKeyCredentialToJSON(response) })
    } catch (error) {
      message.error(error.message)
    }
    // window.navigator.credentials
    //   .create(options)
    //   .then((response) => {
    //     // let makeCredResponse =  publicKeyCredentialToJSON(response)
    //     console.log(response)
    //     // that.setState({ response: response })
    //   })
    //   .catch((error) => message(error))
    // getMakeCredentialsChallenge({ username, name })
    //   .then((response) => {
    //     let publicKey = preformatMakeCredReq(response)
    //     that.setState({ options: { publicKey } })
    //     return window.navigator.credentials.create({ publicKey })
    //   })
    //   .then((response) => {
    //     let makeCredResponse = publicKeyCredentialToJSON(response)
    //     return sendWebAuthnResponse(makeCredResponse)
    //   })
    //   .then((response) => {
    //     message.success("ok")
    //     // if (response.status === "ok") {
    //     //   loadMainContainer()
    //     // } else {
    //     //   alert(
    //     //     `Server responed with error. The message is: ${response.message}`
    //     //   )
    //     // }
    //   })
    //   .catch((error) => alert(error))
  }

  handleLogin = (values) => {
    let username = values.username

    if (!username) {
      message.error("Username is missing!")
      return
    }

    const that = this

    getGetAssertionChallenge({ username })
      .then((response) => {
        console.log(response)
        let publicKey = preformatGetAssertReq(response)
        that.setState({ options: { publicKey } })

        return window.navigator.credentials.get({ publicKey })
      })
      .then((response) => {
        console.log()
        let getAssertionResponse = publicKeyCredentialToJSON(response)
        return sendWebAuthnResponse(getAssertionResponse)
      })
      .then((response) => {
        message.success("OK")
        console.log(response)
        // if (response.status === "ok") {
        //   loadMainContainer()
        // } else {
        //   alert(
        //     `Server responed with error. The message is: ${response.message}`
        //   )
        // }
      })
      .catch((error) => alert(error))
  }
  getCommand = (state) => {
    const commands = {
      echo: {
        description: "Echo a passed string.",
        usage: "echo",
        fn: function () {
          return JSON.stringify(state)
        },
      },
    }
    return commands
  }

  render() {
    const { options, user, response, challenge } = this.state
    const command = this.getCommand(challenge)
    return (
      <div className="card-container">
        <Tabs type="card">
          <TabPane tab="Register" key="1">
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Form
                onFinish={this.handleRegister}
                {...layout}
                id="form_register"
              >
                <Form.Item
                  label="User Name"
                  name="username"
                  rules={[
                    { required: true, message: "Please input your username!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item
                  label="Name"
                  name="name"
                  rules={[
                    { required: true, message: "Please input your name!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item {...tailLayout}>
                  <Button type="primary" htmlType="submit" form="form_register">
                    Register
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </TabPane>
          <TabPane tab="Login" key="2">
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Form onFinish={this.handleLogin} id="form_update">
                <Form.Item
                  label="User Name"
                  name="username"
                  rules={[
                    { required: true, message: "Please input your username!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item {...tailLayout}>
                  <Button type="primary" htmlType="submit" form="form_update">
                    Login
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </TabPane>
        </Tabs>
        {response ? (
          <Editor value={{ options, user, challenge, response }}></Editor>
        ) : (
          <div></div>
        )}
        <Terminal
          commands={command}
          welcomeMessage={"Welcome to the React terminal!"}
          promptLabel={"me@React:~$"}
        ></Terminal>
      </div>
    )
  }
}
export default App
